@import "../../index.scss";

.tour {
  background: $primaryGrey;
  border-radius: 0.8rem;
  margin: 1rem 0;

  img {
    width: 100%;
    border-top-left-radius: 0.8rem;
    border-top-right-radius: 0.8rem;
    display: block;
  }
}

.img-container {
  position: relative;

  &:hover .close-btn {
    opacity: 1;
  }
}

.close-btn {
  position: absolute;
  display: block;
  top: 5%;
  right: 5%;
  color: $primaryGreen;
  font-size: 2rem;
  cursor: pointer;
  transition: all 0.3s linear;
  opacity: 0;
}

.tour-info {
  padding: 1rem 0.5rem;

  h3 {
    color: $primaryGreen;
    text-transform: uppercase;
  }
  h4 {
    color: $primaryBlack;
    text-transform: capitalize;
    margin: 0.2rem 0 0.6rem;
  }
  h5 {
    text-transform: capitalize;

    span {
      color: $primaryGreen;
      margin: 0.5rem;
      font-size: 1.2rem;
      cursor: pointer;
    }
  }
}
